import { defineComponent } from "@vue/composition-api";
// import { mapGetters } from "vuex";
import axios from "axios";

import Loader from "@/components/Loader/Loader.vue";
import { Character } from "@/interfaces";

export default defineComponent({
	name: "CharacterDetail",
	components: {
		Loader,
	},
	data() {
		return {
			character: {} as Character,
		};
	},
	mounted() {
		this.getCharacterDetail();
	},
	computed: {
		// ...mapGetters(["characterDetail"]),
		currentCharacter() {
			return this.$store.state.currentCharacter;
		},
		loading() {
			return this.$store.state.loading.characterDetail;
		},
	},
	methods: {
		async getCharacterDetail() {
			const params = this.$route.params;
			const id = parseInt(params.id);
			const characters = this.$store.state.characters;
			/**
			 * By default, we use data in local store
			 */
			this.character = characters.find((c: { id: number }) => c.id === id);
			/**
			 * But if the user reloads the page without going through the character list
			 * Or if characters list is empty
			 */
			if (!this.character) {
				const url = `https://rickandmortyapi.com/api/character/${id}`;
				const response = await axios.get(url);
				this.character = response.data;
			}

			this.$store.commit("setLoading", { loadingKey: "characterDetail", value: false });
		},
	},
});
